.App{
  width: 80%;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.filter__buttons{
  display: flex;
  justify-content: flex-start;
  margin-bottom: 20px;
  text-align: right;
}

.filter__button{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  padding: 5px 10px 5px 10px;
  font-size: 18px;
  border: none;
  background-color: transparent;
  color: #ffffff;
  cursor: pointer;
  text-align: right;
}

.filter__button:hover{
  opacity: 0.7;
  transition: all 0.6s ease-in;
}

.list{
  margin: 0;
  padding: 0;
  width: 70%;
  list-style-type: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
}




